import React from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardSubtitle,
  CardText,
  CardTitle,
  Row,
} from 'reactstrap';
import IntlMessages from '../../helpers/IntlMessages';
import { Separator } from './CustomBootstrap';

const getBadgeColor = (status) => {
  if (status === 'UPCOMING') return 'outline-success';
  if (status === 'ON GOING') return 'outline-warning';
  if (status === 'COMPLETED') return 'outline-danger';
  return 'primary';
};

const RecentHack = ({ data }) => {
  const { title, status, createDate } = data;
  return (
    <Card>
      <CardBody>
        <CardTitle>
          <p className="lead">{title}</p>
        </CardTitle>

        <CardSubtitle>
          <IntlMessages id="hackcard.starts" />
        </CardSubtitle>
        <CardText>{createDate}</CardText>

        <CardSubtitle>
          <IntlMessages id="hackcard.ends" />
        </CardSubtitle>
        <CardText>{createDate}</CardText>

        <Badge color={getBadgeColor(status)} pill outline className="mb-3">
          {status}
        </Badge>

        <Separator className="mb-3" />

        <Row className="justify-content-center">
          <Button>
            <IntlMessages id="hackcard.participate" />
          </Button>
        </Row>
      </CardBody>
    </Card>
  );
};

export default RecentHack;
